.cards {
    display: inline-block;
    transition: transform .2s;
    position: relative;
    border-radius: 70px;
    overflow: hidden;
    margin: 10px; /* add margin here */
    cursor: pointer;
    min-width: 200px;
    height: 300px;
    z-index: 0;
    border: 1px solid rgb(36, 56, 170);
}


.cards:hover {
    transform: scale(1.2);
    z-index: 1000;
    box-shadow: rgba(65, 28, 167, 0.212) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.cards__img {
    height: 300px;
    border-radius: 10px;
}

.cards__overlay {
    position: absolute;
    padding: 0 1rem 1rem 1rem;
    bottom: 0px;
    height: 290px;
    display: flex;
    flex-direction: column;
    width: 85%;
    justify-content: flex-end;
    background: linear-gradient(to bottom, rgba(0,0,0,0), rgba(0,0,0,0.7));
    opacity: 0;
    transition: opacity .2s;
    border-radius: 0 0 10px 10px;
}

.cards__overlay:hover {
    opacity: 1;
}

.card__title {
    font-weight: 900;
    font-size: 1rem;
    margin-bottom: 0.4rem;
    color: #ffffff;
}

.card__runtime {
    font-size: .75rem;
    margin-bottom: 0.25rem;
    color: #cccccc;
}

.card__rating {
    float: right;
    color: #ffc107;
}

.card__description {
    font-style: italic;
    font-size: .75rem;
    margin-bottom: 0.25rem;
    color: #ffffff;
}
